<script setup lang="ts">
import type {Surreal} from "surrealdb";

type Option = {
  id: any, name: string
}
const props = defineProps<{ record: string, label: string, messages:any}>();
const model = defineModel();
const items = ref<any[]>();
onMounted(async () => {
  //
  const db = await useApp().value;
  const results = await db.query<Option[][]>("select id,name from type::table($record)", {record: props.record})
  items.value = results[0];
})
</script>

<template>
  <v-select
      v-model="model"
      :items="items"
      item-title="name"
      item-value="id"
      :label="label"
      :messages="messages"
      single-line
  ></v-select>
</template>

