import type {Surreal} from "surrealdb";
import type {TableField} from "~/types/db";

export async function updateById(data: any, fields: TableField[], db: Surreal) {
    let sql = `update ${data.id}
               set `
    for (const field of fields) {
        if (field.key === 'id') {
            continue;
        }
        if (!data[field.key]) {
            continue;
        }
        if (field.type === 'record') {
            sql += field.key + '=' + data[field.key].id || data[field.key] + ',';
        } else {
            sql += field.key + '=\'' + data[field.key] + '\',';
        }
    }
    sql = sql.substring(0, sql.length - 1);
    const updated = await db.query<any[]>(sql);
    console.log(updated);
}