<script setup lang="ts">
import type {TableField, TableStructure} from "~/types/db";
import {StringRecordId, type Surreal} from "surrealdb";
import {updateById} from "~/utils/db";
import {PutObjectCommand, S3} from "@aws-sdk/client-s3";

const props = defineProps<{ modelType: string, searchKey?: string[] }>()

const queryData = ref<Record<string, any>>({})

const loading = ref<boolean>(false)
const editLoading = ref<boolean>(false)
const dialog = ref<boolean>(false)
const dialogDelete = ref<boolean>(false)
const formTitle = ref<string>("")
const modelName = ref<string>("")
const searchInput = ref<string>("")
const errorMsg = ref<string[]>([])

// 
const headers = ref<TableField[]>([])
const formFields = ref<TableField[]>([]);


const serverItems = ref<any[]>([]);
const editedItem = ref<any>({});
const totalItems = ref<number>(0);
let db: Surreal;
let fs: S3;
onMounted(async () => {
  // INFO FOR TABLE user STRUCTURE

  if (headers.value.length > 0) {
    return
  }
  loading.value = true;
  db = await useApp().value;
  const table = await db.select<TableStructure>(new StringRecordId(`dataTable:${props.modelType}`));
  modelName.value = table.name;
  headers.value = table.dataFields.map((s) => {
    if (!s.readonly) {
      formFields.value.push(s)
    }
    return s;
  })
  loading.value = false;
  queryData.value.search = '';
  headers.value.push({title: '', key: 'actions', sortable: false, align: 'end'});
  fs = await useFs().value;
})

//groupBy,itemsPerPage,page,search,sortBy
async function loadData(data: any) {
  if (headers.value.length === 0) {
    return
  }
  loading.value = true;
  const sortBy = data.sortBy && data.sortBy.length > 0 ? `ORDER BY ${data.sortBy.map((sort: any) => sort.key + ' ' + sort.order).join(',')}` : '';
  const groupBy = data.groupBy && data.groupBy.key ? `GROUP BY ${data.groupBy.key}` : '';

  const fetchField = headers.value.filter(f => f.type === 'record').map(f => f.key).join(',');
  const fetch = fetchField ? ` FETCH ${fetchField}` : '';
  const formWhere = `from ${props.modelType} ${groupBy} `
  const tableData = await db.query<any[]>(`select count() as totalNum ${formWhere} ;select * ${formWhere} ${sortBy} LIMIT ${data.page * data.itemsPerPage} START ${(data.page - 1) * data.itemsPerPage} ${fetch};`);
  // console.log(tableData);
  totalItems.value = tableData[0][0]?.totalNum || 0;
  serverItems.value = tableData[1] || [];
  loading.value = false;
}

function editItem(item: {}) {
  const data = {id: item['id']};
  for (const field of formFields.value) {
    data[field.key] = item[field.key];
  }
  editedItem.value = data;
  formTitle.value = "";
  dialog.value = true;
}

function showAddItem() {
  editedItem.value = {};
  formTitle.value = "";
  dialog.value = true;
}

function deleteItem(item: {}) {
  editedItem.value = item;
  dialogDelete.value = true;
}

async function uploadFiles(v: any, field: string) {
  const files: File | File[] = v[field];
  let fsName = '';
  if (files instanceof Array) {
    const names = [];
    for (const file of files) {
      names.push(file.name);
      await fs.putObject({
        Bucket: props.modelType,
        ContentType: file.type,
        Key: `${props.modelType}/${v.id}/${field}/${file.name}`,
        Body: file,
      });
    }
    fsName = names.join(';');
  } else if (files instanceof File) {
    fsName = files.name;
    //
    await fs.putObject({
      Bucket: props.modelType,
      ContentType: files.type,
      Key: `${props.modelType}/${v.id}/${field}/${files.name}`,
      Body: files,
    });
  }
  v[field] = fsName;
}

async function save() {
  editLoading.value = true;
  const showErr = (err: any) => {
    if (err instanceof Error) {
      errorMsg.value[0] = err.message;
      editLoading.value = false;
      throw err;
    } else {
      console.log(err);
    }
  }
  for (const valueKey in editedItem.value) {
    for (const formField of formFields.value) {
      if (formField.key === valueKey && formField.type === 'file') {
        //
        uploadFiles(editedItem.value, valueKey);
      }
    }
  }
  if (editedItem.value.id) {

    await updateById(editedItem.value, formFields.value, db).catch(showErr);
  } else {
    await db.insert(props.modelType, editedItem.value).catch(showErr);
  }
  queryData.value.search = editedItem.value.name || '';
  editLoading.value = false;
  dialog.value = false;
  editedItem.value = {};
}

async function deleteItemConfirm() {
  loading.value = true;
  if (editedItem.value.id) {
    await db.delete(editedItem.value.id);
  }
  queryData.value.search = '';
  dialogDelete.value = false;
  loading.value = false;
  editedItem.value = {};
}
</script>

<template>

  <v-data-table-server
      v-model:items-per-page="queryData.size"
      :headers="headers"
      :items="serverItems"
      :items-length="totalItems"
      :loading="loading"
      :search="queryData.search"
      item-value="id"
      @update:options="loadData"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ props }">
            <v-btn
                class="mb-2"
                color="primary"
                dark
                v-bind="props"
                @click="showAddItem"
            >
              {{ modelName }}
            </v-btn>
          </template>
          <v-card :loading="editLoading">
            <v-card-title>
              <span class="text-h5">{{
                  formTitle + modelName + (editedItem.id ? '(' + editedItem.id + ')' : '')
                }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col v-for="formField in formFields" cols="12" md="6" sm="8">
                    <v-select v-if="formField.type==='select'"
                              v-model="editedItem[formField.key]"
                              :clearable="formField.clearable"
                              :label="formField.title"
                              :items="formField.items"
                              :multiple="formField.multiple"
                              :messages="formField===formFields[formFields.length-1]?errorMsg:''"
                    ></v-select>
                    <v-file-input v-model="editedItem[formField.key]"
                                  :label="formField.title"
                                  :messages="formField===formFields[formFields.length-1]?errorMsg:''"
                                  v-else-if="formField.type==='file'"
                                  :multiple="formField.multiple"></v-file-input>
                    <common-record-select v-else-if="formField.type==='record'"
                                          v-model="editedItem[formField.key]"
                                          :label="formField.title"
                                          :record="formField.record"
                                          :messages="formField===formFields[formFields.length-1]?errorMsg:''"
                    ></common-record-select>
                    <v-text-field v-else
                                  v-model="editedItem[formField.key]"
                                  :label="formField.title"
                                  :messages="formField===formFields[formFields.length-1]?errorMsg:''"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                  color="blue-darken-1"
                  variant="text"
                  @click="dialog=false;editedItem={}"
              >
                
              </v-btn>
              <v-btn
                  color="blue-darken-1"
                  variant="text"
                  @click="save"
              >
                
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">
              {{ modelName + '(' + (editedItem.name || editedItem.id || '') + ')' }} 
            </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue-darken-1" variant="text" @click="dialogDelete=false;editedItem={}"></v-btn>
              <v-btn color="blue-darken-1" variant="text" @click="deleteItemConfirm"></v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-spacer></v-spacer>
        <v-divider
            class="mx-4"
            inset
            vertical
        ></v-divider>
        <v-text-field
            v-model="searchInput"
            label=""
            @keyup.enter="queryData.search=searchInput"
            prepend-inner-icon="mdi-magnify"
            variant="outlined"
            hide-details
            single-line
        />
      </v-toolbar>

    </template>

    <template v-slot:item="{ item }">
      <tr>
        <td v-for="head of headers" :style="`text-align: ${head.align||'start'}`">
          <div v-if="head.key==='actions'">
            <v-icon
                class="me-2"
                size="small"
                @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
                size="small"
                @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </div>
          <v-tooltip v-if="item[head.key]&&item[head.key].id" :text="item[head.key].type">
            <template v-slot:activator="{ props }">
              <v-btn v-bind="props" class="mb-2"
                     color="primary" variant="text"
                     dark>{{ item[head.key].name }}
              </v-btn>
            </template>
          </v-tooltip>
          <span v-else-if="item[head.key] instanceof Date">{{ item[head.key].toLocaleString() }}</span>
          <span v-else>{{ item[head.key] }}</span>
        </td>
      </tr>
    </template>

    <template v-slot:no-data>
      
    </template>
  </v-data-table-server>

</template>

